// Updated IngredientCombiner.tsx with Add Ingredient functionality
import React, { useState, useEffect } from 'react';
import {
    Paper, Typography, Box, Grid, FormControl, InputLabel,
    Select, MenuItem, Button, Alert, CircularProgress,
    TextField, Divider, Switch, FormControlLabel
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { Ingredient } from '../../models/Ingredient';
import { useInventory } from '../../contexts/InventoryContext';
import { useDrinks } from '../../contexts/DrinkContext';

interface IngredientCombinerProps {
    ingredients: Ingredient[];
}

const IngredientCombiner: React.FC<IngredientCombinerProps> = ({ ingredients }) => {
    // State for add ingredient
    const [newIngredientName, setNewIngredientName] = useState('');
    const [newIngredientCategory, setNewIngredientCategory] = useState('');
    const [inStock, setInStock] = useState(true);
    const [addResult, setAddResult] = useState<{ success: boolean; message: string } | null>(null);
    const [isAddingIngredient, setIsAddingIngredient] = useState(false);

    // Existing combine ingredients state
    const [sourceId, setSourceId] = useState<string>('');
    const [targetId, setTargetId] = useState<string>('');
    const [isProcessing, setIsProcessing] = useState(false);
    const [result, setResult] = useState<{ success: boolean; message: string } | null>(null);
    const [affectedDrinks, setAffectedDrinks] = useState<string[]>([]);

    const { combineIngredients, categories, addIngredient } = useInventory();
    const { drinks } = useDrinks();

    useEffect(() => {
        setTargetId('');
        setResult(null);

        if (sourceId) {
            // Find drinks that use the source ingredient
            const affected = drinks.filter(drink =>
                drink.ingredients.some(i => i.ingredientId === sourceId)
            ).map(drink => drink.name);

            setAffectedDrinks(affected);
        } else {
            setAffectedDrinks([]);
        }
    }, [sourceId, drinks]);

    const handleAddIngredient = async () => {
        if (!newIngredientName.trim()) {
            setAddResult({
                success: false,
                message: 'Please enter an ingredient name.'
            });
            return;
        }

        setIsAddingIngredient(true);
        setAddResult(null);

        try {
            // Find highest existing ID and increment by 1
            const highestId = ingredients.reduce((max, ingredient) => {
                const idNum = parseInt(ingredient.id);
                return isNaN(idNum) ? max : Math.max(max, idNum);
            }, 0);

            const newId = (highestId + 1).toString();

            // Create new ingredient using the correct interface
            const newIngredient: Ingredient = {
                id: newId,
                name: newIngredientName.trim(),
                category: newIngredientCategory || 'Other',
                inStock: inStock
            };

            await addIngredient(newIngredient);
            setAddResult({
                success: true,
                message: `Ingredient "${newIngredientName}" added successfully.`
            });

            // Reset form
            setNewIngredientName('');
            setNewIngredientCategory('');
            setInStock(true);
        } catch (error) {
            setAddResult({
                success: false,
                message: `Error adding ingredient: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        } finally {
            setIsAddingIngredient(false);
        }
    };

    const handleCombine = async () => {
        if (!sourceId || !targetId) {
            setResult({
                success: false,
                message: 'Please select both a source and target ingredient.'
            });
            return;
        }

        setIsProcessing(true);
        setResult(null);

        try {
            await combineIngredients(sourceId, targetId);
            setResult({
                success: true,
                message: 'Ingredients successfully combined! All recipes have been updated.'
            });

            // Reset selections after successful operation
            setSourceId('');
            setTargetId('');
        } catch (error) {
            setResult({
                success: false,
                message: `Error combining ingredients: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        } finally {
            setIsProcessing(false);
        }
    };

    const sortedIngredients = [...ingredients].sort((a, b) =>
        a.name.localeCompare(b.name)
    );

    const sortedCategories = [...categories].sort((a, b) =>
        a.name.localeCompare(b.name)
    );

    const targetIngredients = sortedIngredients.filter(i => i.id !== sourceId);

    return (
        <>
            <Paper sx={{ p: 3, mb: 3 }}>
                <Typography variant="h6" sx={{ mb: 2 }}>Add New Ingredient</Typography>
                <Typography variant="body2" sx={{ mb: 3 }}>
                    Add a new ingredient to your inventory.
                </Typography>

                <Grid container spacing={2}>
                    <Grid size={{xs: 12, sm: 6}}>
                        <TextField
                            fullWidth
                            label="Ingredient Name"
                            value={newIngredientName}
                            onChange={(e) => setNewIngredientName(e.target.value)}
                        />
                    </Grid>
                    <Grid size={{xs: 12, sm: 6}}>
                        <FormControl fullWidth>
                            <InputLabel id="new-category-label">Category</InputLabel>
                            <Select
                                labelId="new-category-label"
                                value={newIngredientCategory}
                                onChange={(e) => setNewIngredientCategory(e.target.value as string)}
                                label="Category"
                            >
                                <MenuItem value="">
                                    <em>Other</em>
                                </MenuItem>
                                {sortedCategories.map((category) => (
                                    <MenuItem key={category.name} value={category.name}>
                                        {category.name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>

                <Box sx={{ mt: 2 }}>
                    <FormControlLabel
                        control={
                            <Switch
                                checked={inStock}
                                onChange={(e) => setInStock(e.target.checked)}
                                color="primary"
                            />
                        }
                        label="In Stock"
                    />
                </Box>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleAddIngredient}
                        startIcon={<AddIcon />}
                        disabled={isAddingIngredient}
                    >
                        {isAddingIngredient ? <CircularProgress size={24} /> : 'Add Ingredient'}
                    </Button>
                </Box>

                {addResult && (
                    <Alert
                        severity={addResult.success ? 'success' : 'error'}
                        sx={{ mt: 2 }}
                        onClose={() => setAddResult(null)}
                    >
                        {addResult.message}
                    </Alert>
                )}
            </Paper>

            <Divider sx={{ my: 3 }} />

            <Paper sx={{ p: 3, mb: 3 }}>
                <Typography variant="h6" sx={{ mb: 2 }}>Combine Ingredients</Typography>

                <Typography variant="body2" sx={{ mb: 3 }}>
                    This tool lets you merge similar ingredients. The source ingredient will be removed,
                    and all drinks using it will be updated to use the target ingredient instead.
                </Typography>

                <Grid container spacing={2}>
                    <Grid size={{xs: 12, sm: 6}}>
                        <FormControl fullWidth>
                            <InputLabel id="source-ingredient-label">Source Ingredient (to remove)</InputLabel>
                            <Select
                                labelId="source-ingredient-label"
                                value={sourceId}
                                onChange={(e) => setSourceId(e.target.value as string)}
                                label="Source Ingredient (to remove)"
                            >
                                {sortedIngredients.map((ingredient) => (
                                    <MenuItem key={ingredient.id} value={ingredient.id}>
                                        {ingredient.name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid size={{xs: 12, sm: 6}}>
                        <FormControl fullWidth disabled={!sourceId}>
                            <InputLabel id="target-ingredient-label">Target Ingredient (to keep)</InputLabel>
                            <Select
                                labelId="target-ingredient-label"
                                value={targetId}
                                onChange={(e) => setTargetId(e.target.value as string)}
                                label="Target Ingredient (to keep)"
                            >
                                {targetIngredients.map((ingredient) => (
                                    <MenuItem key={ingredient.id} value={ingredient.id}>
                                        {ingredient.name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>

                {affectedDrinks.length > 0 && (
                    <Box sx={{ mt: 2, mb: 2 }}>
                        <Typography variant="subtitle2">
                            {affectedDrinks.length} drink{affectedDrinks.length !== 1 ? 's' : ''} will be updated:
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                            {affectedDrinks.slice(0, 5).join(', ')}
                            {affectedDrinks.length > 5 ? ` and ${affectedDrinks.length - 5} more...` : ''}
                        </Typography>
                    </Box>
                )}

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleCombine}
                        disabled={!sourceId || !targetId || isProcessing}
                    >
                        {isProcessing ? <CircularProgress size={24} /> : 'Combine Ingredients'}
                    </Button>
                </Box>

                {result && (
                    <Alert
                        severity={result.success ? 'success' : 'error'}
                        sx={{ mt: 2 }}
                        onClose={() => setResult(null)}
                    >
                        {result.message}
                    </Alert>
                )}
            </Paper>
        </>
    );
};

export default IngredientCombiner;