// src/components/barkeep/DrinkEditor.tsx
import React, { useState } from 'react';
import {
    Paper, Typography, Box, Grid, FormControl, InputLabel,
    Select, MenuItem, Button, Alert, TextField, IconButton,
    List, ListItem, ListItemText, Dialog, DialogTitle,
    DialogContent, DialogActions, Table, TableBody,
    TableCell, TableContainer, TableHead, TableRow,Divider
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import { Ingredient } from '../../models/Ingredient';
import { useDrinks } from '../../contexts/DrinkContext';
import { Drink, DrinkIngredient } from '../../models/Drink';

import DrinkCreator from './DrinkCreator'; // Import the DrinkCreator component

interface DrinkEditorProps {
    ingredients: Ingredient[];
}

const DrinkEditor: React.FC<DrinkEditorProps> = ({ ingredients }) => {
    const { drinks, updateDrink } = useDrinks();

    const [selectedDrinkId, setSelectedDrinkId] = useState<string>('');
    const [currentDrink, setCurrentDrink] = useState<Drink | null>(null);

    // Ingredient editor state
    const [editingIngredient, setEditingIngredient] = useState<DrinkIngredient | null>(null);
    const [editIndex, setEditIndex] = useState<number | null>(null);
    const [dialogOpen, setDialogOpen] = useState(false);

    // New ingredient state
    const [newIngredientId, setNewIngredientId] = useState<string>('');
    const [newAmount, setNewAmount] = useState<string>('');
    const [newUnit, setNewUnit] = useState<string>('');
    const [addIngredientDialogOpen, setAddIngredientDialogOpen] = useState(false);

    // Feedback state
    const [result, setResult] = useState<{ success: boolean; message: string } | null>(null);

    const handleDrinkSelection = (drinkId: string) => {
        setSelectedDrinkId(drinkId);
        const drink = drinks.find(d => d.id === drinkId);
        if (drink) {
            setCurrentDrink({ ...drink, ingredients: [...drink.ingredients] });
            setResult(null);
        } else {
            setCurrentDrink(null);
        }
    };

    const handleOpenEditDialog = (ingredient: DrinkIngredient, index: number) => {
        setEditingIngredient({ ...ingredient });
        setEditIndex(index);
        setDialogOpen(true);
    };

    const handleCloseEditDialog = () => {
        setDialogOpen(false);
        setEditingIngredient(null);
        setEditIndex(null);
    };

    const handleUpdateIngredient = () => {
        if (!currentDrink || !editingIngredient || editIndex === null) return;

        const updatedIngredients = [...currentDrink.ingredients];
        updatedIngredients[editIndex] = editingIngredient;

        const updatedDrink = {
            ...currentDrink,
            ingredients: updatedIngredients
        };

        setCurrentDrink(updatedDrink);
        handleCloseEditDialog();
    };

    const handleOpenAddDialog = () => {
        setNewIngredientId('');
        setNewAmount('');
        setNewUnit('');
        setAddIngredientDialogOpen(true);
    };

    const handleCloseAddDialog = () => {
        setAddIngredientDialogOpen(false);
    };

    const handleAddIngredient = () => {
        if (!currentDrink || !newIngredientId) return;

        const ingredient = ingredients.find(i => i.id === newIngredientId);
        if (!ingredient) return;

        const newDrinkIngredient: DrinkIngredient = {
            ingredientId: newIngredientId,
            amount: newAmount || "0",
            unit: newUnit
        };

        const updatedDrink = {
            ...currentDrink,
            ingredients: [...currentDrink.ingredients, newDrinkIngredient]
        };

        setCurrentDrink(updatedDrink);
        handleCloseAddDialog();
    };

    const handleRemoveIngredient = (index: number) => {
        if (!currentDrink) return;

        const updatedIngredients = [...currentDrink.ingredients];
        updatedIngredients.splice(index, 1);

        setCurrentDrink({
            ...currentDrink,
            ingredients: updatedIngredients
        });
    };

    const handleSaveDrink = () => {
        if (!currentDrink) return;

        try {
            updateDrink(currentDrink);
            setResult({
                success: true,
                message: `Drink "${currentDrink.name}" updated successfully.`
            });
        } catch (error) {
            setResult({
                success: false,
                message: `Error updating drink: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        }
    };

    const sortedDrinks = [...drinks].sort((a, b) => a.name.localeCompare(b.name));
    const sortedIngredients = [...ingredients].sort((a, b) => a.name.localeCompare(b.name));

    // Find ingredient names for display
    const getIngredientName = (id: string): string => {
        const ingredient = ingredients.find(i => i.id === id);
        return ingredient ? ingredient.name : 'Unknown Ingredient';
    };

    return (
        <>
            {/* Add the DrinkCreator component here */}
            <DrinkCreator ingredients={ingredients} />

            <Divider sx={{ my: 4 }} />

            <Paper sx={{ p: 3, mb: 3 }}>
                <Typography variant="h6" sx={{ mb: 2 }}>Edit Drink Recipes</Typography>
                <Typography variant="body2" sx={{ mb: 3 }}>
                    Select a drink to modify its ingredients, amounts, and units.
                </Typography>

                <FormControl fullWidth sx={{ mb: 3 }}>
                    <InputLabel id="drink-select-label">Select Drink</InputLabel>
                    <Select
                        labelId="drink-select-label"
                        value={selectedDrinkId}
                        onChange={(e) => handleDrinkSelection(e.target.value as string)}
                        label="Select Drink"
                    >
                        {sortedDrinks.map((drink) => (
                            <MenuItem key={drink.id} value={drink.id}>
                                {drink.name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>

                {currentDrink && (
                    <>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                            <Typography variant="subtitle1">
                                Ingredients for {currentDrink.name}
                            </Typography>
                            <Button
                                variant="outlined"
                                startIcon={<AddIcon />}
                                onClick={handleOpenAddDialog}
                            >
                                Add Ingredient
                            </Button>
                        </Box>

                        <TableContainer component={Paper} variant="outlined" sx={{ mb: 3 }}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>Ingredient</TableCell>
                                        <TableCell align="right">Amount</TableCell>
                                        <TableCell align="right">Unit</TableCell>
                                        <TableCell align="right">Actions</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {currentDrink.ingredients.map((ingredient, index) => (
                                        <TableRow key={`${ingredient.ingredientId}-${index}`}>
                                            <TableCell>{getIngredientName(ingredient.ingredientId)}</TableCell>
                                            <TableCell align="right">{ingredient.amount || '-'}</TableCell>
                                            <TableCell align="right">{ingredient.unit || '-'}</TableCell>
                                            <TableCell align="right">
                                                <IconButton size="small" onClick={() => handleOpenEditDialog(ingredient, index)}>
                                                    <EditIcon fontSize="small" />
                                                </IconButton>
                                                <IconButton size="small" onClick={() => handleRemoveIngredient(index)}>
                                                    <DeleteIcon fontSize="small" />
                                                </IconButton>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>

                        <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={handleSaveDrink}
                            >
                                Save Changes
                            </Button>
                        </Box>
                    </>
                )}

                {!currentDrink && selectedDrinkId && (
                    <Alert severity="error">
                        Could not load drink information. Please try again.
                    </Alert>
                )}
            </Paper>

            {result && (
                <Alert
                    severity={result.success ? 'success' : 'error'}
                    sx={{ mt: 2 }}
                    onClose={() => setResult(null)}
                >
                    {result.message}
                </Alert>
            )}

            {/* Edit Ingredient Dialog */}
            <Dialog open={dialogOpen} onClose={handleCloseEditDialog}>
                <DialogTitle>Edit Ingredient</DialogTitle>
                <DialogContent>
                    <Grid container spacing={2} sx={{ mt: 1 }}>
                        <Grid size={{xs: 12}}>
                            <Typography variant="subtitle2">
                                {editingIngredient ? getIngredientName(editingIngredient.ingredientId) : ''}
                            </Typography>
                        </Grid>
                        <Grid size={{xs: 12, sm: 6}}>
                            <TextField
                                fullWidth
                                label="Amount"
                                type="number"
                                inputProps={{ step: "0.25", min: "0" }}
                                value={editingIngredient?.amount || ''}
                                onChange={(e) => setEditingIngredient(prev =>
                                    prev ? { ...prev, amount: e.target.value || "0" } : null
                                )}
                            />
                        </Grid>
                        <Grid size={{xs: 12, sm: 6}}>
                            <TextField
                                fullWidth
                                label="Unit"
                                value={editingIngredient?.unit || ''}
                                onChange={(e) => setEditingIngredient(prev =>
                                    prev ? { ...prev, unit: e.target.value } : null
                                )}
                            />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseEditDialog}>Cancel</Button>
                    <Button onClick={handleUpdateIngredient} variant="contained" color="primary">Save</Button>
                </DialogActions>
            </Dialog>

            {/* Add Ingredient Dialog */}
            <Dialog open={addIngredientDialogOpen} onClose={handleCloseAddDialog}>
                <DialogTitle>Add Ingredient</DialogTitle>
                <DialogContent>
                    <Grid container spacing={2} sx={{ mt: 1 }}>
                        <Grid size={{xs: 12}}>
                            <FormControl fullWidth>
                                <InputLabel id="new-ingredient-label">Ingredient</InputLabel>
                                <Select
                                    labelId="new-ingredient-label"
                                    value={newIngredientId}
                                    onChange={(e) => setNewIngredientId(e.target.value as string)}
                                    label="Ingredient"
                                >
                                    {sortedIngredients.map((ingredient) => (
                                        <MenuItem key={ingredient.id} value={ingredient.id}>
                                            {ingredient.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid size={{xs: 12, sm: 6}}>
                            <TextField
                                fullWidth
                                label="Amount"
                                type="number"
                                inputProps={{ step: "0.25", min: "0" }}
                                value={newAmount}
                                onChange={(e) => setNewAmount(e.target.value)}
                            />
                        </Grid>
                        <Grid size={{xs: 12, sm: 6}}>
                            <TextField
                                fullWidth
                                label="Unit"
                                value={newUnit}
                                onChange={(e) => setNewUnit(e.target.value)}
                            />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseAddDialog}>Cancel</Button>
                    <Button
                        onClick={handleAddIngredient}
                        variant="contained"
                        color="primary"
                        disabled={!newIngredientId}
                    >
                        Add
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default DrinkEditor;