import React, { createContext, useState, useEffect, useContext, ReactNode } from 'react';
import { Ingredient, Category } from '../models/Ingredient';
import { fetchIngredients, fetchCategories, saveIngredients, getLocalIngredients } from '../services/dataService';

interface InventoryContextType {
    ingredients: Ingredient[];
    categories: Category[];
    isLoading: boolean;
    error: string | null;
    updateIngredient: (updatedIngredient: Ingredient) => void;
    toggleIngredientStock: (id: string, inStock: boolean) => void;
    updateIngredientAmount: (id: string, amount: number) => void;
    shoppingList: string[];
    addToShoppingList: (ingredientId: string) => void;
    removeFromShoppingList: (ingredientId: string) => void;
    markInStock: (ingredientId: string, inStock: boolean) => void;
    isInShoppingList: (ingredientId: string) => boolean;
    addMultipleToShoppingList: (ingredientIds: string[]) => void;
    markAsPurchased: (ingredientId: string) => void;
    combineIngredients: (sourceId: string, targetId: string) => Promise<void>;
    addCategory: (category: Category) => void;
    updateCategory: (oldCategory: Category, newCategory: Category) => void;
    deleteCategory: (categoryId: string) => void;
    addIngredient: (ingredient: Ingredient) => void;
}

const InventoryContext = createContext<InventoryContextType | undefined>(undefined);

interface InventoryProviderProps {
    children: ReactNode;
}

export const InventoryProvider: React.FC<InventoryProviderProps> = ({ children }) => {
    const [ingredients, setIngredients] = useState<Ingredient[]>([]);
    const [categories, setCategories] = useState<Category[]>([]);
    const [isLoading, setIsLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);
    const [shoppingList, setShoppingList] = useState<string[]>([]);

    // Load shopping list from localStorage on init
    useEffect(() => {
        const savedList = localStorage.getItem('shoppingList');
        if (savedList) {
            try {
                setShoppingList(JSON.parse(savedList));
            } catch (e) {
                console.error('Failed to parse shopping list:', e);
            }
        }
    }, []);

    // Load categories from both localStorage and categories.json
    useEffect(() => {
        const loadCategories = async () => {
            try {
                // First try to get categories from localStorage
                const savedCategories = localStorage.getItem('categories');
                if (savedCategories) {
                    try {
                        const parsedCategories = JSON.parse(savedCategories);
                        // Check if it's a valid array
                        if (Array.isArray(parsedCategories) && parsedCategories.length > 0) {
                            setCategories(parsedCategories);
                            return; // Exit early if we successfully loaded from localStorage
                        }
                    } catch (e) {
                        console.error("Error parsing categories from localStorage:", e);
                    }
                }

                // If no valid categories in localStorage, load from categories.json
                const initialCategories = await fetchCategories();
                setCategories(initialCategories);
            } catch (err) {
                console.error("Failed to load categories:", err);
                // Fallback to empty array if both methods fail
                setCategories([]);
            }
        };

        loadCategories();
    }, []);

    // Save shopping list to localStorage when it changes
    useEffect(() => {
        localStorage.setItem('shoppingList', JSON.stringify(shoppingList));
    }, [shoppingList]);

    // Update the useEffect that saves to localStorage
    useEffect(() => {
        // Save categories to localStorage whenever they change
        localStorage.setItem('categories', JSON.stringify(categories));
    }, [categories]);

    useEffect(() => {
        const loadData = async () => {
            try {
                setIsLoading(true);

                // Always fetch the latest ingredients
                const fetchedIngredients = await fetchIngredients();

                // Merge with any local changes to preserve user settings
                const localIngredients = getLocalIngredients();

                if (localIngredients) {
                    // Merge local changes (like inStock status) with new ingredients
                    const mergedIngredients = fetchedIngredients.map(ingredient => {
                        const localIngredient = localIngredients.find(local => local.id === ingredient.id);
                        return localIngredient ? { ...ingredient, inStock: localIngredient.inStock } : ingredient;
                    });
                    setIngredients(mergedIngredients);
                } else {
                    setIngredients(fetchedIngredients);
                }
                setError(null);
            } catch (err) {
                setError('Failed to load inventory data');
                console.error(err);
            } finally {
                setIsLoading(false);
            }
        };

        loadData();
    }, []);

    const getIngredientById = (id: string) => {
        return ingredients.find(ingredient => ingredient.id === id);
    };

    const updateIngredient = (updatedIngredient: Ingredient) => {
        const newIngredients = ingredients.map(ingredient =>
            ingredient.id === updatedIngredient.id ? updatedIngredient : ingredient
        );
        setIngredients(newIngredients);
        saveIngredients(newIngredients);
    };

    const toggleIngredientStock = (id: string, inStock: boolean) => {
        const newIngredients = ingredients.map(ingredient =>
            ingredient.id === id ? { ...ingredient, inStock } : ingredient
        );
        setIngredients(newIngredients);
        saveIngredients(newIngredients);
    };

    const updateIngredientAmount = (id: string, amount: number) => {
        const newIngredients = ingredients.map(ingredient =>
            ingredient.id === id ? { ...ingredient, amount } : ingredient
        );
        setIngredients(newIngredients);
        saveIngredients(newIngredients);
    };

    const addToShoppingList = (ingredientId: string) => {
        if (!shoppingList.includes(ingredientId)) {
            setShoppingList(prev => [...prev, ingredientId]);
        }
    };

    const removeFromShoppingList = (ingredientId: string) => {
        setShoppingList(prev => prev.filter(id => id !== ingredientId));
    };

    const markInStock = (ingredientId: string, inStock: boolean) => {
        const newIngredients = ingredients.map(ingredient =>
            ingredient.id === ingredientId ? { ...ingredient, inStock } : ingredient
        );
        setIngredients(newIngredients);
        saveIngredients(newIngredients);

        // Optionally remove from shopping list when marked in stock
        if (inStock) {
            removeFromShoppingList(ingredientId);
        }
    };

    const isInShoppingList = (ingredientId: string) => {
        return shoppingList.includes(ingredientId);
    };

    const addMultipleToShoppingList = (ingredientIds: string[]) => {
        if (!ingredientIds.length) return;

        // Use the functional update pattern to ensure we're working with the latest state
        setShoppingList(currentList => {
            const newList = [...currentList];

            // Add only ids that aren't already in the list
            ingredientIds.forEach(id => {
                if (!newList.includes(id)) {
                    newList.push(id);
                }
            });

            return newList;
        });
    };
    const markAsPurchased = (ingredientId: string) => {
        // 1. Remove from shopping list
        setShoppingList(prev => prev.filter(id => id !== ingredientId));

        // 2. Mark as in stock in inventory
        const newIngredients = ingredients.map(ingredient =>
            ingredient.id === ingredientId ? { ...ingredient, inStock: true } : ingredient
        );

        setIngredients(newIngredients);
        saveIngredients(newIngredients);
    };
    const combineIngredients = async (sourceId: string, targetId: string): Promise<void> => {
        // Get the source and target ingredients
        const sourceIngredient = ingredients.find(i => i.id === sourceId);
        const targetIngredient = ingredients.find(i => i.id === targetId);

        if (!sourceIngredient || !targetIngredient) {
            throw new Error('Source or target ingredient not found');
        }

        // Update drinks that use the source ingredient (using an event or other mechanism)
        const event = new CustomEvent('combineIngredients', {
            detail: { sourceId, targetId }
        });
        window.dispatchEvent(event);

        // Remove the source ingredient from the list
        const newIngredients = ingredients.filter(i => i.id !== sourceId);
        setIngredients(newIngredients);
    };

    // Fix addCategory
    const addCategory = (category:Category) => {
        if (categories.some(c => c.name === category.name)) {
            throw new Error("Category already exists");
        }

        const newCategories = [...categories, category];
        setCategories(newCategories);
    };

    // Update a category and all ingredients using it
    const updateCategory = (oldCategory: Category, newCategory: Category) => {
        if (oldCategory.name === newCategory.name) {
            // Just updating description, no need to check for duplicates
            const newCategories = categories.map(c =>
                c.name === oldCategory.name ? newCategory : c
            );
            setCategories(newCategories);
            return;
        }

        // Check if the new category name already exists
        if (categories.some(c => c.name === newCategory.name)) {
            throw new Error("Category already exists");
        }

        // Update category in categories list
        const newCategories = categories.map(c =>
            c.name === oldCategory.name ? newCategory : c
        );
        setCategories(newCategories);

        // Update all ingredients with that category
        const updatedIngredients = ingredients.map(ingredient =>
            ingredient.category === oldCategory.name
                ? { ...ingredient, category: newCategory.name }
                : ingredient
        );
        setIngredients(updatedIngredients);
    };

// Fix deleteCategory
    const deleteCategory = (categoryName: string) => {
        // Remove from categories list
        const newCategories = categories.filter(c => c.name !== categoryName);
        setCategories(newCategories);

        // Set affected ingredients to "Other" category
        const updatedIngredients = ingredients.map(ingredient =>
            ingredient.category === categoryName
                ? { ...ingredient, category: "Other" }
                : ingredient
        );
        setIngredients(updatedIngredients);
    };

    const addIngredient = async (ingredient: Ingredient): Promise<void> => {
        // Check if ingredient with same name already exists
        if (ingredients.some(i => i.name.toLowerCase() === ingredient.name.toLowerCase())) {
            throw new Error("Ingredient with this name already exists");
        }

        // Add the new ingredient to the list
        const newIngredients = [...ingredients, ingredient];
        setIngredients(newIngredients);

        // Save the updated ingredients list
        saveIngredients(newIngredients);
    };

    return (
        <InventoryContext.Provider
            value={{
                ingredients,
                categories,
                isLoading,
                error,
                updateIngredient,
                toggleIngredientStock,
                updateIngredientAmount,
                shoppingList,
                addToShoppingList,
                removeFromShoppingList,
                markInStock,
                isInShoppingList,
                addMultipleToShoppingList,
                markAsPurchased,
                combineIngredients,
                addCategory,
                updateCategory,
                deleteCategory,
                addIngredient

            }}
        >
            {children}
        </InventoryContext.Provider>
    );
};

export const useInventory = (): InventoryContextType => {
    const context = useContext(InventoryContext);
    if (context === undefined) {
        throw new Error('useInventory must be used within an InventoryProvider');
    }
    return context;
};