// src/pages/Barkeep.tsx
import React, { useState } from 'react';
import { Container, Box, Tabs, Tab, Typography, Paper } from '@mui/material';
import { useInventory } from '../contexts/InventoryContext';
import PageHeader from '../components/layout/PageHeader';
import IngredientCombiner from '../components/barkeep/IngredientCombiner';
import CategoryManager from '../components/barkeep/CategoryManager';
import DrinkEditor from '../components/barkeep/DrinkEditor';
import { useMediaQuery } from '@mui/material';
import barImage from '../media/barkeep.jpg';

const Barkeep: React.FC = () => {
    const [tabValue, setTabValue] = useState(0);
    const { ingredients, categories, isLoading } = useInventory();

    // Convert Category objects to strings for CategoryManager component
    const categoryNames = categories.map(category => category.name);

    // Call hooks at the component's top level
    const isMobile = useMediaQuery((theme) => theme.breakpoints.down('md'));
    const isDesktop = useMediaQuery((theme) => theme.breakpoints.up('md'));

    const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
        setTabValue(newValue);
    };

    if (isLoading) {
        return (
            <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
                <div>Loading...</div>
            </Container>
        );
    }

    return (
        <>
            <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
                <PageHeader
                    title="Barkeep"
                    subtitle="Manage your bar setup, ingredients, and recipes"
                    imageSrc={barImage}
                />
            </Container>

            <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
                <Paper sx={{ p: 2, mb: 3 }}>
                    <Typography variant="body1" sx={{ mb: 2 }}>
                        Welcome to Barkeep! This tool helps you maintain your bar by combining similar ingredients,
                        managing ingredient categories, and editing drink recipes.
                    </Typography>
                </Paper>

                <Box sx={{ mb: 3, width: '100%' }}>
                    <Tabs
                        value={tabValue}
                        onChange={handleTabChange}
                        indicatorColor="primary"
                        textColor="primary"
                        variant={isMobile ? 'scrollable' : 'standard'}
                        centered={isDesktop}
                        scrollButtons="auto"
                        allowScrollButtonsMobile
                        sx={{
                            '& .MuiTab-root': {
                                minWidth: { xs: 'auto', md: 160 },
                                px: { xs: 1.5, sm: 2 }
                            }
                        }}
                    >
                        <Tab label="Ingredients" />
                        <Tab label="Categories" />
                        <Tab label="Drinks" />
                    </Tabs>
                </Box>

                {tabValue === 0 && (
                    <IngredientCombiner
                        ingredients={ingredients}
                    />
                )}

                {tabValue === 1 && (
                    <CategoryManager
                        categories={categories}
                        ingredients={ingredients}
                    />
                )}

                {tabValue === 2 && (
                    <DrinkEditor
                        ingredients={ingredients}
                    />
                )}
            </Container>
        </>
    );
};

export default Barkeep;