// src/components/barkeep/DrinkCreator.tsx
import React, { useState } from 'react';
import {
    Paper, Typography, Box, Grid, TextField, Button,
    FormControl, InputLabel, Select, MenuItem,
    Chip, Alert, CircularProgress, Divider
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { Drink, DrinkTags } from '../../models/Drink';
import { Ingredient } from '../../models/Ingredient';
import { useDrinks } from '../../contexts/DrinkContext';
import { useInventory } from '../../contexts/InventoryContext';

interface DrinkCreatorProps {
    ingredients: Ingredient[];
}

const DrinkCreator: React.FC<DrinkCreatorProps> = ({ ingredients }) => {
    const { drinks, addDrink } = useDrinks();
    const { ingredients: inventoryIngredients } = useInventory();

    // Form states
    const [name, setName] = useState('');
    const [image, setImage] = useState('');
    const [instructions, setInstructions] = useState('');
    const [selectedIngredients, setSelectedIngredients] = useState<{
        ingredientId: string;
        amount: string;
        unit: string;
    }[]>([]);
    const [carAuctionText, setCarAuctionText] = useState('');

    // Tags states
    const [strength, setStrength] = useState<'light' | 'medium' | 'strong'>('medium');
    const [tasteInput, setTasteInput] = useState('');
    const [tasteTags, setTasteTags] = useState<string[]>([]);

    // UI states
    const [isCreating, setIsCreating] = useState(false);
    const [result, setResult] = useState<{ success: boolean; message: string } | null>(null);

    // For ingredient selection
    const [selectedIngredientId, setSelectedIngredientId] = useState('');
    const [amount, setAmount] = useState('');
    const [unit, setUnit] = useState('oz');

    const handleAddIngredient = () => {
        if (selectedIngredientId && amount) {
            setSelectedIngredients(prev => [...prev, {
                ingredientId: selectedIngredientId,
                amount,
                unit: unit || 'oz'
            }]);

            // Reset ingredient form
            setSelectedIngredientId('');
            setAmount('');
        }
    };

    const handleRemoveIngredient = (index: number) => {
        setSelectedIngredients(prev => prev.filter((_, i) => i !== index));
    };

    const handleAddTasteTag = (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter' && tasteInput.trim()) {
            e.preventDefault();
            const newTag = tasteInput.trim().toLowerCase();
            if (!tasteTags.includes(newTag)) {
                setTasteTags([...tasteTags, newTag]);
            }
            setTasteInput('');
        }
    };

    const handleRemoveTasteTag = (tagToRemove: string) => {
        setTasteTags(tasteTags.filter(tag => tag !== tagToRemove));
    };

    // Then fix the calculateCanMake function:
    const calculateCanMake = () => {
        // If no ingredients in inventory, cannot make drink
        if (!inventoryIngredients || inventoryIngredients.length === 0) {
            return false;
        }

        // Check if all ingredients are in inventory and in stock
        return selectedIngredients.every(ingredient => {
            const inventoryItem = inventoryIngredients.find(
                item => item.id === ingredient.ingredientId
            );
            return inventoryItem && inventoryItem.inStock;
        });
    };

    const handleCreateDrink = async () => {
        if (!name.trim()) {
            setResult({
                success: false,
                message: 'Please provide a name for the drink'
            });
            return;
        }

        if (drinks.some(d => d.name.toLowerCase() === name.toLowerCase())) {
            setResult({
                success: false,
                message: 'A drink with this name already exists'
            });
            return;
        }

        if (selectedIngredients.length === 0) {
            setResult({
                success: false,
                message: 'Please add at least one ingredient'
            });
            return;
        }

        if (tasteTags.length === 0) {
            setResult({
                success: false,
                message: 'Please add at least one taste tag'
            });
            return;
        }

        setIsCreating(true);
        setResult(null);

        try {
            // Find highest existing ID and increment by 1
            const highestId = drinks.reduce((max, drink) => {
                const idNum = parseInt(drink.id);
                return isNaN(idNum) ? max : Math.max(max, idNum);
            }, 0);

            const newId = (highestId + 1).toString();

            // Calculate canMake based on current inventory
            const canMake = calculateCanMake();

            // Create tags object
            const tags: DrinkTags = {
                strength,
                taste: tasteTags,
                canMake
            };

            // Create new drink
            const newDrink: Drink = {
                id: newId,
                name: name.trim(),
                image: image || 'drink-placeholder.jpg',
                instructions: instructions,
                ingredients: selectedIngredients,
                tags,
                carAuctionEasterEgg: carAuctionText || undefined
            };

            await addDrink(newDrink);

            setResult({
                success: true,
                message: `"${name}" has been added to your drinks!`
            });

            // Reset form
            setName('');
            setImage('');
            setInstructions('');
            setSelectedIngredients([]);
            setStrength('medium');
            setTasteTags([]);
            setCarAuctionText('');

        } catch (error) {
            setResult({
                success: false,
                message: `Error creating drink: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        } finally {
            setIsCreating(false);
        }
    };

    const commonUnits = ['oz', 'ml', 'dash', 'tsp', 'tbsp', 'splash', 'part', 'pieces'];
    const sortedIngredients = [...ingredients].sort((a, b) => a.name.localeCompare(b.name));

    return (
        <Paper sx={{ p: 3, mb: 3 }}>
            <Typography variant="h6" sx={{ mb: 2 }}>Create New Drink</Typography>

            <Grid container spacing={2}>
                <Grid size={{xs: 12, sm: 6}}>
                    <TextField
                        fullWidth
                        label="Drink Name"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        required
                    />
                </Grid>

                <Grid size={{xs: 12, sm: 6}}>
                    <TextField
                        fullWidth
                        label="Image URL (optional)"
                        value={image}
                        onChange={(e) => setImage(e.target.value)}
                        placeholder="Leave blank for default image"
                    />
                </Grid>

                <Grid size={{xs: 12}}>
                    <TextField
                        fullWidth
                        label="Instructions"
                        value={instructions}
                        onChange={(e) => setInstructions(e.target.value)}
                        multiline
                        rows={3}
                        placeholder="Describe how to prepare this drink"
                    />
                </Grid>
                <Grid size={{xs: 12}}>
                    <TextField
                        fullWidth
                        label="Car Auction Easter Egg (optional)"
                        value={carAuctionText}
                        onChange={(e) => setCarAuctionText(e.target.value)}
                        multiline
                        rows={2}
                        placeholder="Add a humorous car auction description..."
                        sx={{ mt: 2 }}
                    />
                </Grid>
            </Grid>

            <Box sx={{ mt: 3 }}>
                <Typography variant="subtitle1">Ingredients</Typography>
                <Grid container spacing={2} sx={{ mt: 1 }}>
                    <Grid size={{xs: 12, sm: 5}}>
                        <FormControl fullWidth>
                            <InputLabel id="ingredient-select-label">Ingredient</InputLabel>
                            <Select
                                labelId="ingredient-select-label"
                                value={selectedIngredientId}
                                onChange={(e) => setSelectedIngredientId(e.target.value as string)}
                                label="Ingredient"
                            >
                                {sortedIngredients.map((ingredient) => (
                                    <MenuItem key={ingredient.id} value={ingredient.id}>
                                        {ingredient.name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid size={{xs: 6, sm: 3}}>
                        <TextField
                            fullWidth
                            label="Amount"
                            value={amount}
                            onChange={(e) => setAmount(e.target.value)}
                            placeholder="1.5"
                        />
                    </Grid>

                    <Grid size={{xs: 6, sm: 2}}>
                        <FormControl fullWidth>
                            <InputLabel id="unit-select-label">Unit</InputLabel>
                            <Select
                                labelId="unit-select-label"
                                value={unit}
                                onChange={(e) => setUnit(e.target.value as string)}
                                label="Unit"
                            >
                                {commonUnits.map((unit) => (
                                    <MenuItem key={unit} value={unit}>{unit}</MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid size={{xs: 12, sm: 2}}>
                        <Button
                            fullWidth
                            variant="outlined"
                            onClick={handleAddIngredient}
                            disabled={!selectedIngredientId || !amount}
                            sx={{ height: { xs: '40px', sm: '56px' }}}
                        >
                            Add
                        </Button>
                    </Grid>
                </Grid>

                {selectedIngredients.length > 0 ? (
                    <Box sx={{ mt: 2, display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                        {selectedIngredients.map((item, index) => {
                            const ingredient = ingredients.find(i => i.id === item.ingredientId);
                            return (
                                <Chip
                                    key={index}
                                    label={`${ingredient?.name} - ${item.amount} ${item.unit}`}
                                    onDelete={() => handleRemoveIngredient(index)}
                                    color="primary"
                                    variant="outlined"
                                />
                            );
                        })}
                    </Box>
                ) : (
                    <Alert severity="info" sx={{ mt: 2 }}>No ingredients added yet</Alert>
                )}
            </Box>

            {/* Drink Tags */}
            <Box sx={{ mt: 4 }}>
                <Typography variant="subtitle1" sx={{ mb: 2 }}>Drink Properties</Typography>

                <Grid container spacing={2}>
                    <Grid size={{xs: 12, sm: 6}}>
                        <FormControl fullWidth>
                            <InputLabel id="strength-select-label">Strength</InputLabel>
                            <Select
                                labelId="strength-select-label"
                                value={strength}
                                onChange={(e) => setStrength(e.target.value as 'light' | 'medium' | 'strong')}
                                label="Strength"
                            >
                                <MenuItem value="light">Light</MenuItem>
                                <MenuItem value="medium">Medium</MenuItem>
                                <MenuItem value="strong">Strong</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid size={{xs: 12}}>
                        <TextField
                            fullWidth
                            label="Taste Tags (press Enter to add)"
                            value={tasteInput}
                            onChange={(e) => setTasteInput(e.target.value)}
                            onKeyDown={handleAddTasteTag}
                            placeholder="e.g., sweet, sour, bitter, etc."
                        />

                        <Box sx={{ mt: 2, display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                            {tasteTags.length > 0 ? (
                                tasteTags.map((tag, index) => (
                                    <Chip
                                        key={index}
                                        label={tag}
                                        onDelete={() => handleRemoveTasteTag(tag)}
                                        color="secondary"
                                        variant="outlined"
                                    />
                                ))
                            ) : (
                                <Typography variant="body2" color="text.secondary">
                                    No taste tags added yet
                                </Typography>
                            )}
                        </Box>
                    </Grid>
                </Grid>
            </Box>

            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleCreateDrink}
                    startIcon={<AddIcon />}
                    disabled={isCreating}
                >
                    {isCreating ? <CircularProgress size={24} /> : 'Create Drink'}
                </Button>
            </Box>

            {result && (
                <Alert
                    severity={result.success ? 'success' : 'error'}
                    sx={{ mt: 2 }}
                    onClose={() => setResult(null)}
                >
                    {result.message}
                </Alert>
            )}
        </Paper>
    );
};

export default DrinkCreator;