import React, { useState } from 'react';
import {
    Paper, Typography, Box, Grid, FormControl, InputLabel,
    Select, MenuItem, Button, Alert, TextField, IconButton,
    List, ListItem, ListItemText, Dialog, DialogTitle,
    DialogContent, DialogActions, Chip
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import { Ingredient, Category } from '../../models/Ingredient';
import { useInventory } from '../../contexts/InventoryContext';

interface CategoryManagerProps {
    categories: Category[];
    ingredients: Ingredient[];
}

const CategoryManager: React.FC<CategoryManagerProps> = ({ categories, ingredients }) => {
    // State for category operations
    const [newCategoryName, setNewCategoryName] = useState('');
    const [newCategoryDescription, setNewCategoryDescription] = useState('');
    const [selectedCategory, setSelectedCategory] = useState('');
    const [editingCategory, setEditingCategory] = useState<Category | null>(null);
    const [updatedCategoryName, setUpdatedCategoryName] = useState('');
    const [updatedCategoryDescription, setUpdatedCategoryDescription] = useState('');

    // State for ingredient category reassignment
    const [ingredientId, setIngredientId] = useState('');
    const [newCategory, setNewCategory] = useState('');

    // Dialog state
    const [dialogOpen, setDialogOpen] = useState(false);
    const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);

    // Feedback state
    const [result, setResult] = useState<{ success: boolean; message: string } | null>(null);

    const {
        addCategory,
        updateCategory,
        deleteCategory,
        updateIngredient
    } = useInventory();

    const handleCreateCategory = () => {
        if (!newCategoryName.trim()) {
            setResult({
                success: false,
                message: 'Please enter a category name.'
            });
            return;
        }

        try {
            // Create a new category object
            const newCategory = {
                id: newCategoryName.trim().toLowerCase(),
                name: newCategoryName.trim(),
                description: newCategoryDescription.trim()
            };

            addCategory(newCategory);

            setResult({
                success: true,
                message: `Category "${newCategoryName}" created successfully.`
            });
            setNewCategoryName('');
            setNewCategoryDescription('');
        } catch (error) {
            setResult({
                success: false,
                message: `Error creating category: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        }
    };

    const handleOpenEditDialog = (category: Category) => {
        setEditingCategory(category);
        setUpdatedCategoryName(category.name);
        setUpdatedCategoryDescription(category.description || '');
        setDialogOpen(true);
    };

    const handleCloseEditDialog = () => {
        setDialogOpen(false);
        setEditingCategory(null);
        setUpdatedCategoryName('');
        setUpdatedCategoryDescription('');
    };

    const handleEditCategory = () => {
        if (!editingCategory || !updatedCategoryName.trim()) {
            return;
        }

        try {
            const oldCategory = editingCategory;
            const newCategory = {
                ...oldCategory,
                name: updatedCategoryName.trim(),
                description: updatedCategoryDescription.trim()
            };

            updateCategory(oldCategory, newCategory);

            setResult({
                success: true,
                message: `Category renamed to "${updatedCategoryName}".`
            });
            handleCloseEditDialog();
        } catch (error) {
            setResult({
                success: false,
                message: `Error updating category: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        }
    };

    const handleOpenDeleteDialog = (categoryName: string) => {
        setSelectedCategory(categoryName);
        setDeleteDialogOpen(true);
    };

    const handleCloseDeleteDialog = () => {
        setDeleteDialogOpen(false);
        setSelectedCategory('');
    };

    const handleDeleteCategory = () => {
        if (!selectedCategory) return;

        try {
            deleteCategory(selectedCategory);
            setResult({
                success: true,
                message: `Category "${selectedCategory}" deleted successfully.`
            });
            handleCloseDeleteDialog();
        } catch (error) {
            setResult({
                success: false,
                message: `Error deleting category: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        }
    };

    const handleChangeIngredientCategory = () => {
        if (!ingredientId || !newCategory) {
            setResult({
                success: false,
                message: 'Please select both an ingredient and a category.'
            });
            return;
        }

        try {
            const ingredient = ingredients.find(i => i.id === ingredientId);
            if (ingredient) {
                updateIngredient({
                    ...ingredient,
                    category: newCategory
                });
                setResult({
                    success: true,
                    message: `"${ingredient.name}" moved to category "${newCategory}".`
                });
                setIngredientId('');
                setNewCategory('');
            }
        } catch (error) {
            setResult({
                success: false,
                message: `Error updating ingredient category: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
        }
    };

    const sortedCategories = [...categories].sort((a, b) => a.name.localeCompare(b.name));
    const sortedIngredients = [...ingredients].sort((a, b) => a.name.localeCompare(b.name));

    // Get ingredients count for each category
    const categoryCount = sortedCategories.reduce((acc, category) => {
        // Count ingredients that belong to this category
        acc[category.name] = ingredients.filter(ingredient =>
            // Case-insensitive comparison
            ingredient.category?.toLowerCase() === category.name?.toLowerCase()
        ).length;
        return acc;
    }, {} as Record<string, number>);

    return (
        <>
            <Paper sx={{ p: 3, mb: 3 }}>
                <Typography variant="h6" sx={{ mb: 2 }}>Manage Categories</Typography>

                <Grid container spacing={2}>
                    <Grid size={{xs: 12, sm: 9}}>
                        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                            <TextField
                                fullWidth
                                label="New Category Name"
                                value={newCategoryName}
                                onChange={(e) => setNewCategoryName(e.target.value)}
                            />
                            <TextField
                                fullWidth
                                label="Category Description (Optional)"
                                value={newCategoryDescription}
                                onChange={(e) => setNewCategoryDescription(e.target.value)}
                                multiline
                                rows={2}
                            />
                        </Box>
                    </Grid>
                    <Grid size={{xs: 12, sm: 3}}>
                        <Button
                            fullWidth
                            variant="contained"
                            color="primary"
                            onClick={handleCreateCategory}
                            startIcon={<AddIcon />}
                            sx={{ height: '56px', mt: 1 }}
                        >
                            Create
                        </Button>
                    </Grid>
                </Grid>

                <Typography variant="subtitle1" sx={{ mt: 3, mb: 1 }}>Current Categories</Typography>
                <List>
                    {sortedCategories.map((category) => (
                        <ListItem
                            key={category.name}
                            secondaryAction={
                                <Box sx={{ display: 'flex', gap: 1 }}>
                                    <IconButton onClick={() => handleOpenEditDialog(category)}>
                                        <EditIcon />
                                    </IconButton>
                                    <IconButton onClick={() => handleOpenDeleteDialog(category.name)}>
                                        <DeleteIcon />
                                    </IconButton>
                                </Box>
                            }
                            sx={{ pr: 16 }} // Add padding to prevent text overlap with buttons
                        >
                            <ListItemText
                                primary={
                                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                        <Typography variant="body1">{category.name}</Typography>
                                        <Chip
                                            label={`${categoryCount[category.name] || 0}`}
                                            size="small"
                                            color="primary"
                                            variant="outlined"
                                            sx={{ ml: 1 }}
                                        />
                                    </Box>
                                }
                                secondary={
                                    category.description && (
                                        <Typography variant="body2" color="text.secondary">
                                            {category.description}
                                        </Typography>
                                    )
                                }
                            />
                        </ListItem>
                    ))}
                </List>
            </Paper>

            <Paper sx={{ p: 3, mb: 3 }}>
                <Typography variant="h6" sx={{ mb: 2 }}>Reassign Ingredient Category</Typography>
                <Typography variant="body2" sx={{ mb: 3 }}>
                    Change which category an ingredient belongs to.
                </Typography>

                <Grid container spacing={2}>
                    <Grid size={{xs: 12, sm: 6}}>
                        <FormControl fullWidth>
                            <InputLabel id="ingredient-label">Select Ingredient</InputLabel>
                            <Select
                                labelId="ingredient-label"
                                value={ingredientId}
                                onChange={(e) => setIngredientId(e.target.value as string)}
                                label="Select Ingredient"
                            >
                                {sortedIngredients.map((ingredient) => (
                                    <MenuItem key={ingredient.id} value={ingredient.id}>
                                        {ingredient.name} ({ingredient.category})
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid size={{xs: 12, sm: 6}}>
                        <FormControl fullWidth>
                            <InputLabel id="category-label">New Category</InputLabel>
                            <Select
                                labelId="category-label"
                                value={newCategory}
                                onChange={(e) => setNewCategory(e.target.value as string)}
                                label="New Category"
                            >
                                {sortedCategories.map((category) => (
                                    <MenuItem key={category.name} value={category.name}>
                                        {category.name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleChangeIngredientCategory}
                        disabled={!ingredientId || !newCategory}
                    >
                        Change Category
                    </Button>
                </Box>
            </Paper>

            {result && (
                <Alert
                    severity={result.success ? 'success' : 'error'}
                    sx={{ mt: 2 }}
                    onClose={() => setResult(null)}
                >
                    {result.message}
                </Alert>
            )}

            {/* Edit Category Dialog */}
            <Dialog open={dialogOpen} onClose={handleCloseEditDialog}>
                <DialogTitle>Edit Category</DialogTitle>
                <DialogContent>
                    <TextField
                        autoFocus
                        margin="dense"
                        label="Category Name"
                        fullWidth
                        value={updatedCategoryName}
                        onChange={(e) => setUpdatedCategoryName(e.target.value)}
                    />
                    <TextField
                        margin="dense"
                        label="Category Description"
                        fullWidth
                        multiline
                        rows={3}
                        value={updatedCategoryDescription}
                        onChange={(e) => setUpdatedCategoryDescription(e.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseEditDialog}>Cancel</Button>
                    <Button onClick={handleEditCategory} variant="contained" color="primary">Save</Button>
                </DialogActions>
            </Dialog>

            {/* Delete Category Dialog */}
            <Dialog open={deleteDialogOpen} onClose={handleCloseDeleteDialog}>
                <DialogTitle>Delete Category</DialogTitle>
                <DialogContent>
                    <Typography>
                        Are you sure you want to delete the category "{selectedCategory}"?
                    </Typography>
                    <Typography color="error" sx={{ mt: 2 }}>
                        Warning: This will reset the category for {categoryCount[selectedCategory] || 0} ingredients to "Other".
                    </Typography>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseDeleteDialog}>Cancel</Button>
                    <Button onClick={handleDeleteCategory} variant="contained" color="error">Delete</Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default CategoryManager;